import React from 'react';
import { Header, Footer } from '../../components';
import { Outlet } from 'react-router-dom';
import styles from './AppLayout.module.css';

function AppLayout() {
  return (
    <div className={styles.scrollContainer}>
        <Header/>
        <Outlet/>
        <Footer/>
    </div>
  )
}

export default AppLayout;
