import React from 'react';
import { Routes, Route } from "react-router-dom";
// import './App.less';

import { useAppStore } from './stores/AppStore';
import { AppLayout, HomePage, ProfilePage, Surf } from './pages';

function App() {
  const [{ profile = null }] = useAppStore()
  return (
    <Routes>
      <Route path="/" element={<AppLayout/>}>
        <Route index element={<HomePage/>} />
        <Route path="/profile" element={profile ? <ProfilePage data={profile}/> : <Surf/>} />
      </Route>
    </Routes>
  );
}

export default App;
