import React from 'react';
import styles from './MainMenu.module.css';
import { useAppStore } from '../../stores/AppStore';
import { Button } from '../../elements';
import { Profile } from '../../components';

interface IMainMenu {
  vertical: boolean;
}

function MainMenu({vertical}: IMainMenu) {
  const [{
    profile = null,
  },,{
    signIn, signOut
  }] = useAppStore()

  const signInClick = async () => {
    console.log('signInClick')
    await signIn()
  }
  
  const signOutClick = async () => {
    console.log('signOutClick')
    await signOut()
  }

  return (
    <nav className={vertical ? `${styles.MainMenu} ${styles.vertical}` : styles.MainMenu}>
      <Button link="/auctions">
        Auction</Button>
      <Button link="#" disabled>
        Events</Button>
      <div className={styles.spacer}/>
      <div className={styles.groupItems}>
        <Button wide primary className={styles.grow}>Create Numi</Button>
        { 
          profile
            ? <Profile data={profile} onClick={() => signOutClick()}/>
            : <Button wide onClick={() => signInClick()}>Sign in</Button>
        }
      </div>
    </nav>
  )
}

export default MainMenu;
