import React from 'react';
import './Button.css';
import { Link } from 'react-router-dom';
import styles from './Button.module.css';

interface IButton {
  wide?: boolean;
  link?:string;
  primary?: boolean;
  bordered?: boolean;
  disabled?: boolean;
  onClick?: Function;
  className?: string;
  [prop: string]: any;
}

function Button({
  wide = false, 
  link = '',
  primary = false,
  className = '',
  children = null,
  onClick = () => {},
  disabled = false,
  ...props
}: IButton) {
  console.log(`${[
    styles.Link,
    wide ? styles.wide : '',
    primary ? styles.primary : '',
    className,
  ].filter(c => !!c ).join(' ')}`)
  return link ? (
    <Link {...{
      ...props,
      to: link,
      className: `${[
        styles.Link,
        wide ? styles.wide : '',
        primary ? styles.primary : '',
        disabled ? styles.disabled : '',
        className,
      ].filter(c => !!c ).join(' ')}`,
    }}>{children}</Link>
  ) : (
    <button {...{
      onClick: (e) => {
        console.log(e)
        onClick(e)
      }, 
      className: `${[
        styles.Button,
        wide ? styles.wide : '',
        primary ? styles.primary : '',
        disabled ? styles.disabled : '',
        className,
    ].filter(c => !!c ).join(' ')}`
  }}>{ children }</button>
  )
}

export default Button;
