import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from "react-router-dom";
import './i18n';
import './index.css';
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';
import { AppStoreProvider } from './stores/AppStore';
import { createServer } from "miragejs"

if(process.env.USE_MIRAGE) {
  
  createServer({
    routes() {

      function serverAuth(res: object) {
        console.log({res})
        let Profile = localStorage.getItem('Profile')
        if(Profile !== null) return JSON.parse(Profile);
        Profile = JSON.stringify(res)
        localStorage.setItem('Profile', Profile)
        return Profile
      }
    
      this.get("/auth/on", () => {
        console.log("/auth/on", serverAuth({ success: true })); 
        return serverAuth({ success: true })
      }, { timing: 500 })
      this.get("/auth/off", () => {
        console.log("/auth/off", serverAuth({ success: false })); 
        return serverAuth({ success: false })
      }, { timing: 500 })

      // this.post("/api/Profile", (schema: any, request: {requestBody: string}) => {
      //   localStorage.setItem('Profile', request.requestBody)
      //   if(false) console.log(schema)
      //   return {success: true, message: "Saved!"}
      // }, { timing: 500 })

    },
  }) 
}

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <AppStoreProvider>
        <App />
      </AppStoreProvider>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

serviceWorkerRegistration.register();
reportWebVitals();
