import { TReducers, TScenarios, createDatanomy } from 'react-datanomy';

type TPage = {
  route: string;
  [property: string]: any;
};

type TError = typeof Error;

interface IAppStore {
  errors: Array<TError>;
  profile: object | null;
  page: TPage;
  // showcase: Array<object>;
  // pageItems: number;
  // page: number;
  [property: string]: any;
}

const AppStore: IAppStore = {
  errors: [],
  profile: null,
  page: {
    route: '/'
  }
}

const reducers: TReducers<IAppStore> = {
  error: (store, error) => ({ ...store, errors: [...store.errors, error] }),
  logIn: (store, profile) => ({ ...store, profile }),
  logOut: (store) => ({ ...store, profile: null }),
  setPage: (store, page: TPage) => ({ ...store, page }),

}

const scenarios: TScenarios<IAppStore> = {
  signIn: async (getContext: Function, user: object) => {
    const [{profile = null}, {logIn, error}] = getContext()
    if(!profile) {
      const res = await fetch('/auth/on')
      console.log({res})

      const data = await res.json()
      console.log({data})
      if(data.success) logIn(data); else error(new Error('Net: Authentication failed'))
      console.log('signed in:', user)
    }
  },
  
  signOut: async (getContext: Function, user: object) => {
    const [{profile}, {logOut, error}] = getContext()
    if(profile) {
      const res = await fetch('/auth/off')
      const data = await res.json()
      if(data.success) logOut(data); else error(new Error('Net: Sign out failed'))
      console.log('signed out:', user)
    }
  },

  // registerPage: async (getContext: Function, page: TPage) => {
  //   const [state,{setPage}] = getContext()
  //   if(state.page.route !== page.route) setPage(page)
  // },
}

export const [
  AppStoreProvider,
  useAppStore,
  AppStoreContext
] = createDatanomy<IAppStore>(AppStore, reducers, scenarios)
